<template>
     <div class="my-popups modal right fade show" style="display:block" role="dialog">
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog doc-setup-slide">
                <div class="modal-content">
                    <div class="modal-header mheader-user py-0">
                    <button type="button" class="close" @click="close()">
                        <i aria-hidden="true" class="fa fa-times"></i>
                    </button>
                    <h4 class="modal-title py-3">{{ $t('Configuration') }}</h4>
                </div>
                  <div class="modal-body bg-white">
                        <section class="container-fluid p-0">
                            <div class="left-menu-tab new_tab">
                                
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                                    
                                    <ul v-if="popupCondition=='Configuration'" class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                        <li class="nav-item FourthScreen" @click="SkipTab(1)">
                                            <a class="nav-link left-menu" :class="{'active':step==1}" style="cursor: pointer;">
                                                <i class="fa fa-user pr-2 text-muted" aria-hidden="true"></i>
                                                <span class="font-14">{{$t('General')}}</span>
                                            </a>
                                        </li>
                                        <li class="nav-item SecondScreen" @click="SkipTab(2)">
                                            <a class="nav-link left-menu" :class="{'active':step==2}" style="cursor: pointer;">
                                                <i class="fa fa-check pr-2 text-muted" aria-hidden="true"></i>
                                                <span class="font-14">{{$t('Approval')}}</span>
                                            </a>
                                        </li>
                                        <li class="nav-item ThirdScreen" @click="SkipTab(3)">
                                            <a class="nav-link left-menu" :class="{'active':step==3}" style="cursor: pointer;">
                                                <i class="fa fa-plus pr-2 text-muted" aria-hidden="true"></i>
                                                <i class="fa fa-check-circle font-18 pr-2 text-success" v-if="IsClientStoragesCopmleted == true" aria-hidden="true"></i>
                                                <span class="font-14">{{$t('ProductManagement')}}</span>
                                            </a>
                                        </li>
                                    </ul>

                                </div>
                            </div>
                            <div class="right-content">
                                
                                   <div class="col-md-12 p-0" v-if="popupCondition=='Configuration'">
                                     <Configuration :CurrentTab="CurrentTab" v-if="popupCondition == 'Configuration'"></Configuration>
                                       </div>
                            </div>

                        </section>
                  </div>
                </div>
            </div>
        </div>
     </div>
</template>
<script>
import Configuration from "../../views/AdvancedSetup/Configuration.vue"
export default {
    components:{
        Configuration
    },
    data() {
        return {
            step:1,
            click:'SetupAsset',
            CurrentTab:1,
            IsClientStoragesCopmleted:false,
        }
    },
    mounted: function(){
        var vm= this;
        $('#modalManageView').on('blur', function(){
             this.IsVisible=false;
        });
    },
    props:{
       IsVisible:{
            type: Boolean,
            required: true
        },
        popupCondition:{
            type: String,
            required: false
        }
    },
    watch: {
        IsVisible: {
            handler(val, older) {
                if (val) {
                    this.IsVisible=val
                }

            }
        }
    },
    methods:{
         close: function () {
            this.$parent.IsshowConfiguration=false;
        },
        SkipTab:function(s){
            debugger
            var vm=this;
            vm.step=s;
            vm.CurrentTab=s;
        },
        BackToStep: function(s){
            var vm=this;
            vm.step=s;  
        }
    }
}
</script>

<style>
.newtab_des .nav-link.left-menu.active {
    background: #e0f2ff !important;
}

</style>